@import "sass/index";

.root {
  color: $m_secondary-main;
  width: 100%;
  height: 100%;
  position: relative;
}
.coins {
  display: flex;
}
.coinImg {
  position: relative;
  width: 20px;
}
.title {
  margin: 0;
}
.text {
  margin: 0;
}
.subTitle {
  margin: 0;
  font-size: 0.75rem;
  font-weight: 400;
  opacity: 0.75;
}
.checkBox {
  overflow: hidden;
  transition: max-height 0.4s;
  height: 50px;
  max-height: 50px;
}
.switchBox {
  margin-top: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.switchToggle {
  color: #090c16 !important;
}
.switchLabel {
  max-width: 90px;
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 12px;
  color: white;
  letter-spacing: 0.86px;
  text-align: right;
  transition: color $defaultTransition;
}
.switchLabelLeft {
  text-align: right;
}
.switchLabelRight {
  text-align: left;
}
.selectedSwitchLabel {
  color: rgba(white, 0.5);
}
.amountBox {
  position: relative;
}
.buttonAmount {
  position: absolute;
  top: 34px;
  bottom: 10px;
  right: 4px;
  display: flex;

  & > button {
    height: 41px;
    margin-right: 5px;
  }
}
.amountInput {
  padding-right: 150px;
}
.button {
  height: 41px;
  background: rgba(white, 0.1);
  color: rgba(white, 0.5);
}
.sendButton {
  width: 100%;
  max-width: 300px;
}
.sendWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.feesWrapper {
  display: flex;
  justify-content: center;
}
.feesInnerWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  align-items: end;
  justify-items: end;
  max-width: 370px;

  @media (max-width: 400px) {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
.fee {
  font-family: Helvetica;
  color: rgba(white, 0.5);
  font-size: 13px;
  margin-top: 34px;
  white-space: nowrap;
}
.feeHelp {
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
}
.disabled {
  background: #d5d5d5;
}

.track {
  background: white;
}
